* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  font-family: 'Lato', sans-serif;
}

.App {
  padding: 12px;
  text-align: center;
  background: -webkit-gradient(linear, left bottom, left top, from(#e15f41),color-stop(47%, #e15f41),to(#c44569)) repeat-y;
  background: -webkit-linear-gradient(bottom, #e15f41 0%,#e15f41 47%,#c44569 100%) repeat-y;
  background: -o-linear-gradient(bottom, #e15f41 0%,#e15f41 47%,#c44569 100%) repeat-y;
  background: linear-gradient(to top, #e15f41 0%,#e15f41 47%,#c44569 100%) repeat-y;
  height: 100vh;
}

.App__title {
  margin: 1em;
  color: #fff;
}

.btn-container {
  margin: 100px auto 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  max-width: 600px;
}

.btn,
.select {
  width: 90px;
  height: 35px;
  border: 1px solid #e77f67;
  border-radius: 4px;
  outline: 0;
  background: transparent;
  text-transform: lowercase;
  font-size: 90%;
  color: #ecf0f1;
  -webkit-transition: border-color 0.3s ease-in-out, color 0.3s ease-in-out;
  -o-transition: border-color 0.3s ease-in-out, color 0.3s ease-in-out;
  transition: border-color 0.3s ease-in-out, color 0.3s ease-in-out;
  cursor: pointer;
}

.btn:hover,
.select:hover,
.select:focus {
  border-color: #f19066;
}
.select:hover {
  background-color: #e15f41;
  outline-color: #e15f41;
}

@media (max-width: 1024px) {
  .btn-container {
    margin-top: 40px;
    width: 70%;
  }
}
@media (max-width: 992px) and (orientation: landscape){
  .App {
    height: 100%;
  }
  .btn-container {
    margin: 30px auto 40px;
  }
}
@media (max-width: 768px) {
  .btn-container {
    margin-bottom: 30px;
    width: 90%;
  }
}

@media (max-width: 580px) {
  .btn-container {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin: 20px 0 30px;
    width: 100%;
  }
  .btn,
  .select {
    margin-bottom: 10px;
  }
}


